.personal {
  [data-tab-content],
  [data-inner-content] {
    display: none;

    &.show {
      display: block;
    }
  }

  .btn-black {
    padding-left: 27px;
    padding-right: 27px;
    width: auto;
    max-width: none;

    @media (max-width: 375px) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  .input {
    margin-bottom: 16px;

    @include media-breakpoint-down(md) {
      margin-bottom: 14px;
    }

    input {
      padding: 18px 0 3px;
    }
  }

  h3 {
    margin-bottom: 12px;
    font-weight: 800;
    font-size: 16px;
    line-height: 28px;
    color: var(--basic-gray);
    text-transform: uppercase;
  }

  &__wrapper {
    margin: 0 auto;
    padding: 118px 0 70px;
    width: 100%;
    max-width: 857px;

    @include media-breakpoint-down(md) {
      padding: 20px 0 40px;
    }
  }

  &__nav {
    width: 29.9%;

    @include media-breakpoint-down(md) {
      margin-bottom: 18px;
      width: 100%;
    }

    a {
      margin-bottom: 10px;
      font-weight: 800;
      font-size: 16px;
      line-height: 28px;
      color: var(--basic-gray);
      text-transform: uppercase;
      transition: 0.3s ease-out;

      &.active {
        color: var(--basic-green);
        text-decoration: none;
      }

      &:hover {
        color: var(--basic-green);
        text-decoration: none;
      }

      @include media-breakpoint-down(md) {
        line-height: 1;
      }

      &:not(:last-of-type) {
        position: relative;

        @include media-breakpoint-down(md) {
          padding-right: 50px;

          &:after {
            position: absolute;
            top: 0;
            right: 25px;

            content: "";
            width: 2px;
            height: 100%;
            background-color: var(--basic-gray);
          }
        }
      }
    }
  }

  &__content {
    width: 64.3%;

    @include media-breakpoint-down(md) {
      width: 100%
    }
  }

  &__form {
    margin-bottom: 30px;
    padding-bottom: 40px;
    border-bottom: 1px solid var(--basic-gray);

    p {
      margin-bottom: 30px;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--basic-gray);
    }

    .btn {
      margin-top: 10px;

      @include media-breakpoint-down(md) {
        margin-bottom: 6px;
      }
    }

    &:last-of-type {
      .btn {

        @include media-breakpoint-down(md) {
          align-self: flex-start;
        }
      }
    }
  }

  &__card {
    width: 100%;
    max-width: 251px;

    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    color: var(--basic-gray);

    display: grid;
    grid-template-columns: 26.3%  68.5%;
    grid-column-gap: 13px;
    align-items: center;
    grid-auto-rows: min-content;
    grid-template-areas:
      "picture name"
      "picture number";

    img {
      grid-area: picture;
    }

    @include media-breakpoint-down(md) {
      margin-bottom: 20px;
    }
  }

  &__card-name {
    grid-area: name;
  }

  &__card-number {
    grid-area: number;
  }

  &__order {
    p {
      margin-bottom: 0;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--basic-gray);
    }
  }

  &__order-wrap {
    margin-top: 30px;

    @include media-breakpoint-down(md) {
      margin-top: 20px;
    }
  }

  &__order-nav {
    padding-bottom: 28px;
    border-bottom: 1px solid var(--basic-gray);

    a {
      width: 50%;
      font-weight: 400;
      font-size: 13px;
      line-height: 1;
      color: var(--basic-gray);
      text-transform: uppercase;
      transition: 0.3s ease-out;
      text-align: center;

      &.active {
        text-shadow: 0 0 0.7px  var(--basic-gray), 0 0 0.7px  var(--basic-gray), 0 0 0.7px  var(--basic-gray);
        text-decoration: underline;
      }

      &:hover {
        text-shadow: 0 0 0.7px  var(--basic-gray), 0 0 0.7px  var(--basic-gray), 0 0 0.7px  var(--basic-gray);
        text-decoration: underline;
      }
    }
  }

  .order {
    padding: 16px 0;
    border-bottom: 1px solid var(--basic-gray);

    @include media-breakpoint-down(md) {
      padding: 20px 0;
      justify-content: space-between;
    }

    .btn {
      align-self: center;
      margin-left: 10px;

      @include media-breakpoint-down(md) {
        margin-left: 0;
        width: 47.8%;
      }
    }

    time {
      font-weight: 400;
      font-size: 10px;
      line-height: 13px;
      color: var(--basic-gray);
    }

    p {
      margin-bottom: 0;
    }

    &__info {
      margin-right: auto;
      align-items: flex-start;
      justify-content: flex-start;

      @include media-breakpoint-down(md) {
        margin-bottom: 7px;
        align-items: center;
        justify-content: space-between;
        width: 100%;
      }
    }

    &__products {
      font-weight: 400;
      font-size: 10px;
      line-height: 13px;
      color: var(--basic-gray);
    }

    &__number {
      font-weight: 800;
      font-size: 13px;
      line-height: 16px;
      color: var(--basic-gray);
      text-transform: uppercase;
    }

    &__status {
      font-weight: 800;
      font-size: 13px;
      line-height: 16px;
      color: var(--basic-gray);
      text-transform: uppercase;
    }

    &--delivered {
      .order__status {
        color: var(--basic-green);
      }
    }

    &--cancelled {
      .order__status {
        color: var(--basic-red);
      }
    }
  }
}
