.product-slider {
  padding: 44px 0 63px;
  background-color: rgba(#5dbd00, 0.2);

  @include media-breakpoint-down(md) {
    padding: 25px 0 29px;
  }

  .slider-nav {
    top: 50%;
    transform: translateY(-50%);
  }

  h2 {
    margin-bottom: 25px;
    font-weight: 800;
    font-size: 20px;
    line-height: 1;
    letter-spacing: 0.01em;
    color: #000;
    text-transform: uppercase;

    @include media-breakpoint-down(md) {
      margin-bottom: 12px;
      font-size: 18px;
      line-height: 27px;
    }
  }

  li {
    height: 100%;
  }

  .slick-slide {
    margin-right: 45px;
    height: auto;

    @include media-breakpoint-down(md) {
      margin-right: 20px;
    }

    & > div {
      height: 100%;
    }
  }

  .slick-track {
    display: flex;
  }
}
