.product-card {
  padding-bottom: 57px;

  @include media-breakpoint-down(md) {
    padding-bottom: 30px;
  }

  h2 {
    margin-bottom: 7px;
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    color: #000;
    letter-spacing: 0.02em;

    @include media-breakpoint-down(md) {
      margin-bottom: 2px;
      font-size: 18px;
      line-height: 27px;
    }
  }
  .product-tabs {
    width: 100%;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 58.2%  33.3%;
    grid-column-gap: 88px;
    grid-template-areas:
      "slider info"
      "slider form"
      "tab tab";

    @include media-breakpoint-down(md) {
      grid-template-columns: 100%;
      grid-column-gap: 0;
      grid-template-areas:
        "slider"
        "info"
        "tab"
        "form";
    }
  }

  &__slider {
    //width: 58.2%;
    grid-area: slider;
    display: flex;
    flex-direction: column;

    @include media-breakpoint-down(md) {
      width: 100%;
    }
  }

  &__info {
    grid-area: info;

    @include media-breakpoint-down(md) {
      width: 100%;
    }
  }

  &__form {
    grid-area: form;
    margin-top: auto;
  }

  &__tab {
    margin-top: 50px;
    grid-area: tab;

    @include media-breakpoint-down(md) {
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }

  &__badge {
    top: 0;
    right: 0;
    border: 23px solid transparent;
    border-top: 23px solid #eb5757;
    border-right: 23px solid #eb5757;
    text-align: center;

    span {
      position: absolute;
      top: -18px;
      left: -8px;
      font-weight: 900;
      font-size: 10px;
      line-height: 1;
      letter-spacing: 0.01em;
      color: #fff;
      transform: rotate(45deg);
    }
  }

  &__search {
    position: relative;
    width: 100%;
    margin-bottom: 72px;

    @include media-breakpoint-down(md) {
      margin-bottom: 0;
    }

    input {
      border: none;
      outline: none;
      background-color: transparent;
      transition: 0.3s ease-out;

      &:focus {
        outline: none;
      }
    }

    input[type=text] {
      width: 100%;
      border-bottom: 1px solid #fff;
      transition: 0.3s ease-out;

      &:focus {
        border-bottom: 1px solid #000;
      }
    }
  }

  &__search-submit {
    top: 50%;
    transform: translateY(-50%);
    right: 0;

    width: 18px;
    height: 18px;

    svg {
      position: absolute;
      top: 50%;
      transform: translateY(-50%) translateX(-50%);
      left: 50%;
      transition: 0.3s ease-out;
    }

    input[type=submit] {
      position: absolute;
      top: 50%;
      transform: translateY(-50%) translateX(-50%);
      left: 50%;
      z-index: 1;
      cursor: pointer;
    }

    &:hover {
      svg {
        opacity: 0.7;
      }

      & + label {
        input {
          border-bottom: 1px solid #000;
        }
      }
    }
  }

  &__description {
    margin-bottom: 4px;
    font-size: 18px;
    line-height: 1;
    color: var(--basic-gray-medium);
    font-weight: 700;
    text-transform: uppercase;
    display: flex;

    @include media-breakpoint-down(md) {
      margin-bottom: 2px;
      font-size: 14px;
      line-height: 21px;
    }

    p {
      margin-right: 11px;
      letter-spacing: 0.02em;
    }

    p + p {
      margin-right: 0;
      font-weight: 400;
      letter-spacing: -0.01em;
    }
  }

  &__price {
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    color: var(--basic-green);
    letter-spacing: -0.01em;
    display: flex;

    @include media-breakpoint-down(md) {
      margin-bottom: 12px;
      font-size: 18px;
      line-height: 27px;
    }
  }

  .rating {
    margin-bottom: 20px;

    @include media-breakpoint-down(md) {
      margin-bottom: 5px;
    }

    &__container {
      margin-right: 7px;
    }

    p {
      display: flex;
      color: var(--basic-gray-medium);
    }
  }

  .stepper {
    margin-bottom: 13px;
  }

  .form-selects {
    margin-top: auto;
  }

  &--with-table {
    .product-card__wrapper {
      grid-template-areas:
        "slider info"
        "slider tab"
        "slider tab"
        "table table";

      @include media-breakpoint-down(lg) {
        grid-template-columns: 48.2%  47.2%;
        grid-column-gap: 38px;
      }

      @include media-breakpoint-down(md) {
        grid-template-columns: 100%;
        grid-template-areas:
          "slider"
          "info"
          "tab"
          "table";
      }
    }

    .product-table {
      margin-top: 48px;
      grid-area: table;

      @include media-breakpoint-down(md) {
        margin-top: 30px;
      }
    }

    .product-card__tab {
      margin-top: 53px;

      @include media-breakpoint-down(md) {
        margin-top: 20px;
      }
    }

    .product-card__price {
      margin-bottom: 0;
    }

    .product-tabs__link {
      padding: 9px 7px;

      @include media-breakpoint-down(md) {
        padding: 9px 4px;
      }
    }
  }
}
