.login-modal {
  display: none;
  position: fixed;
  z-index: 3;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.7);
  &.visible {
    display: block;
  }
  form {
    margin-top: 10px;
    a {
      color: white;
      font-size: 18px;
      text-decoration: underline;
      &:hover {
        color: white;
      }
    }
    button {
      background-color: #000;
      font-size: 24px;
      font-weight: 700;
      line-height: 21px;
      letter-spacing: -0.01em;
      margin-top: 55px;
      height: 60px;
      border: none;
    }
  }
  label {
    font-size: 18px;
    font-weight: 700;
    line-height: 18px;
    letter-spacing: -0.022em;
    margin-bottom: 11px;
    margin-top: 15px;
  }
  input {
    margin-bottom: 11px;
    height: 45px;
    font-size: 18px;
    padding-left: 16px;
    padding-right: 16px;
    border: none;
  }
}
.login-modal__area {
  max-width: 528px;
  padding: 36px 50px 56px;
  color: white;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 15;
  @media (max-width: 475px) {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
  h3 {
    font-weight: 800;
    text-align: center;
    text-transform: uppercase;
  }
}
