.tienda-links {
  overflow: hidden;

  .container {
    @include media-breakpoint-down(md) {
      padding: 0;
    }
  }

  &__list {
    margin-right: -72px;
    width: calc(100% + 72px);

    @include media-breakpoint-down(lg) {
      margin-right: -30px;
      width: calc(100% + 30px);
    }

    @include media-breakpoint-down(md) {
      margin-right: 0;
      width: 100%;
    }
  }

  &__link {
    margin-right: 72px;
    padding: 13px 0;
    width: calc(100% / 6 - 72px);
    align-items: center;
    min-height: 112px;

    border: 1px solid var(--basic-green);
    color: var(--basic-green);
    transition: 0.3s ease-out;

    @include media-breakpoint-down(lg) {
      margin-right: 30px;
      width: calc(100% / 6 - 30px);
    }

    @include media-breakpoint-down(md) {
      margin-right: 0;
      padding: 7px;
      width: calc(100% / 6);
      min-height: 63px;
    }

    .current-color {
      transition: 0.3s ease-out;
    }
    &.active {
      background-color: var(--basic-green);
      color: #fff;
      text-decoration: none;

      .tienda-links__icon {
        filter: brightness(0) invert(1);
      }
    }
    &:hover {
      background-color: var(--basic-green);
      color: #fff;
      text-decoration: none;

      .tienda-links__icon {
        filter: brightness(0) invert(1);
      }
    }

    span {
      font-size: 18px;
      font-weight: 600;
      line-height: 1;
      letter-spacing: -0.022em;
      pointer-events: none;
      max-width: 105px;
      text-align: center;
      display: flex;

      @include media-breakpoint-down(md) {
        font-size: 9px;
      }
    }
  }

  &__icon {

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    & + span {
      margin-top: auto;
    }
  }
}
