.product-customer {
  .tienda-links {
    margin-top: 72px;
    margin-bottom: 28px;

    @include media-breakpoint-down(md) {
      margin-top: 0;
      width: 100%;
    }
  }

  .breadcrumbs {
    margin-bottom: 12px;
  }

  .slider-thumbnails {
    margin-top: auto;

    @include media-breakpoint-down(md) {
      margin-bottom: 20px;
    }

    &__content {
      margin-bottom: 100px;

      @include media-breakpoint-down(md) {
        margin-bottom: 25px;
      }
    }

    &__slide {
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &__nav {
      .slick-slide {
        margin-right: 15px;
        cursor: pointer;

        @include media-breakpoint-down(md) {
          margin-right: 7px;
          height: 54px;
        }

        img {
          object-fit: cover;

          @include media-breakpoint-down(md) {
            width: 100%;
            height: 54px;
          }
        }
      }
    }

    .slider-nav {
      bottom: 44px;

      @include media-breakpoint-down(md) {
        bottom: 28px;
      }
    }
  }
}
