
.header {
  width: 100%;
  height: 100px;
  display: grid;
  grid-template-columns: auto 1fr auto;
  @include media-breakpoint-down(md) {
    display: flex;
    justify-content: space-between;
  }
}
.header__logo {
  display: flex;
  @include media-breakpoint-down(md) {
    display: none;
  }
}
.hero__page-link-inicio {
  &:hover {
    text-decoration: none;
  }
  @include media-breakpoint-down(md) {
    display: none;
  }
}
.tienda {
  .subscribe {
    @include media-breakpoint-down(md) {
      border-top: 1px solid var(--basic-gray);
    }
  }
}
