.hero-tienda {
  display: flex;
  justify-content: space-between;
  height: 664px;
  background: url("images/index/hero-bg.png") center center no-repeat;
  background-size: cover;
  @include media-breakpoint-down(md) {
    height: 364px;
  }
  .header__logo-img {
    display: none;
  }
}
.hero-tienda__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero-tienda--no-img-desktop {
  height: auto;
  background: none;

  @include media-breakpoint-down(md) {
    height: 364px;
    background: url("images/index/hero-bg.png") center center no-repeat;
  }

  .hero-tienda__logo {
    width: 152px;
    height: 28px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include media-breakpoint-down(md) {
      width: auto;
      height: auto;
    }
  }
}
