.subscribe {
  padding: 21px 0 7px;
  @include media-breakpoint-down(md) {
    padding-top: 0;
  }
}
.subscribe__block {
  background: url("images/subscribe/books.png") center left 50px no-repeat;
  background-size: contain;
  @include media-breakpoint-down(md) {
    padding-top: 53px;
    padding-bottom: 200px;
    background: white url("images/subscribe/books.png") center center no-repeat;
  }
  @include media-breakpoint-down(sm) {
    background-position: center center;
    padding-top: 33px;
  }
}
.subscribe__title {
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  letter-spacing: 0.2px;
  margin-bottom: 5px;
  margin-top: 14px;
}
.subscribe__year {
  font-size: 36px;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: -1px;
  color: white;
  @include media-breakpoint-down(md) {
    color: var(--basic-gray);
  }
}
.subscribe__wrapper {
  display: grid;
  grid-template-columns: 1fr 55%;
  @include media-breakpoint-down(md) {
    grid-template-columns: 1fr;
  }
}
.subscribe__form {
  margin-top: 26px;
  margin-bottom: 60px;
}
.subscribe__form-title {
  font-size: 14px;
  font-weight: 800;
  line-height: 20px;
  margin-bottom: 14px;
  text-transform: uppercase;
}
.subscribe__form-input {
  border: 1px solid var(--neon);
  color: white;
  height: 40px;
  margin-right: 16px;
  padding: 0 16px;
  &::placeholder {
    color: white;
  }
  @include media-breakpoint-down(sm) {
    margin-right: 0;
  }
}
.subscribe__form-area {
  display: grid;
  grid-template-columns: 1fr minmax(0, 117px);
  @include media-breakpoint-down(sm) {
    grid-template-columns: 1fr;
    row-gap: 16px;
  }
}
.subscribe__form-button {
  border: none;
  padding: 0 18px;
  height: 40px;
}
.subscribe__form-text {
  margin-top: 16px;
  font-size: 12px;
  font-weight: 600;
  line-height: 16px;
  letter-spacing: 0.2px;
}
