.preference {
  padding: 141px 0;
  @include media-breakpoint-down(md) {
    padding: 50px 0;
  }
  h2 {
    color: #000;
  }
}
.preference__row {
  margin-top: 40px;
}
.preference__card {
  height: 222px;
  justify-content: space-around;
  &-1 {
    background: rgba(196, 196, 196, 0.3);
    @include media-breakpoint-down(sm) {
      margin-bottom: 40px;
    }
  }
  &-2 {
    background: rgba(93, 189, 0, 0.1);
  }
}

.preference__card-title {
  font-size: 40px;
  font-weight: 900;
  line-height: 32px;
  letter-spacing: 0.001em;
  margin-left: 5px;
}
.preference__card-text {
  font-size: 20px;
  font-weight: 900;
  line-height: 20px;
  letter-spacing: 0.001em;
  color: var(--basic-gray);
  margin-bottom: 24px;

  span {
    color: var(--basic-green);
  }
}
