.cart-content {

  &__wrapper {
    padding: 28px 0 66px;

    @include media-breakpoint-down(md) {
      padding: 20px 0 26px;
    }
  }

  &__back {
    margin-bottom: 44px;
    padding-left: 35px;
    display: inline-block;
    font-weight: 400;
    font-size: 18px;
    line-height: 1;
    color: #000;
    text-transform: uppercase;
    transition: 0.3s ease-out;

    &::before {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      content: "<";
      width: 18px;
      height: 18px;
      color: #000;
    }

    &:hover {
      color: #000;
      text-decoration: none;
      opacity: 0.7;
    }
  }

  &__info {
    width: 62.7%;

    @include media-breakpoint-down(md) {
      width: 100%;
    }

    table {
      margin-top: 60px;
      width: 100%;

      @include media-breakpoint-down(md) {
        margin-top: 0;
      }

      thead {
        @include media-breakpoint-down(md) {
          display: none;
        }
      }

      img {
        max-width: 60px;

        @include media-breakpoint-down(md) {
          max-width: none;
        }
      }

      th {
        padding: 5px 20px;
        font-weight: 700;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
        color: #000;

        @include media-breakpoint-down(lg) {
          padding: 5px;
        }

        &:first-of-type {
          padding-left: 0;
        }

        &:last-of-type {
          padding-right: 0;
        }
      }

      tr {
        @include media-breakpoint-down(md) {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
        }
      }

      td {
        padding: 5px 17px;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
        color: #000;

        @include media-breakpoint-down(lg) {
          padding: 5px;
        }

        @include media-breakpoint-down(md) {
          position: relative;
          padding-top: 30px;
          display: block;
          font-size: 12px;
          line-height: 14px;

          &::before {
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            content: attr(data-content);
            font-weight: 700;
            color: #000;
          }
        }

        @media (max-width: 375px) {
          font-size: 11px;
        }

        &:first-of-type {
          padding-left: 0;

          @include media-breakpoint-down(md) {
            order: -2;
            margin-bottom: 5px;
            padding-top: 0;
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            width: 100%;

            &::before {
              position: static;
              left: auto;
              top: auto;
              transform: none;
              order: -2;
              width: 55%;
              text-align: right;
              padding-right: 5px;
            }
          }

          span {
            @include media-breakpoint-down(md) {
              order: -1;
              width: 45%;
              color: var(--basic-green);
              font-weight: 700;
              text-align: left;
            }
          }
        }

        &:nth-of-type(3) {
          @include media-breakpoint-down(md) {
            margin-bottom: 20px;
            padding-top: 0;
            order: -1;
            display: flex;
            justify-content: center;
            width: 100%;
          }

          &::before {
            position: static;
            left: auto;
            top: auto;
            transform: none;
            padding-right: 3px;
          }
        }

        &:last-of-type {
          padding-right: 0;

          @include media-breakpoint-down(md) {
            color: var(--basic-green);
          }
        }

        span {
          display: none;

          @include media-breakpoint-down(md) {
            display: block;
          }
        }
      }
    }
  }

  &__aside {
    padding: 29px 23px 23px;
    width: 35.5%;
    background-color: rgba(#5dbd00, 0.2);

    @include media-breakpoint-down(md) {
      margin-top: 60px;
      width: 100%;
    }

    .btn {
      margin-top: auto;

      @media (max-width: 375px) {
        font-size: 15px;
      }
    }

    &--with-warning {
      padding-top: 0;

      @include media-breakpoint-down(md) {
        margin-top: 30px;
      }
    }
  }

  h3 {
    margin-bottom: 22px;
    width: 100%;
    font-weight: 800;
    font-size: 18px;
    line-height: 1;
    letter-spacing: -0.022em;
    color: #000;
    text-transform: uppercase;
  }

  fieldset {
    margin-bottom: 60px;
    justify-content: space-between;

    @include media-breakpoint-down(md) {
      margin-bottom: 30px;
    }

    &:last-of-type {
      margin-bottom: 0;
    }

    p {
      width: 100%;

      &:not(:last-of-type) {
        margin-bottom: 20px;
      }

      a {
        position: relative;
        color: #000;
        transition: 0.3s ease-out;

        &::after {
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 1px;
          content: "";
          background-color: #000;
          transition: 0.3s ease-out;
        }

        &:hover {
          color: #000;
          opacity: 0.7;
          text-decoration: none;

          &::after {
            background-color: rgba(#000, 0.7);
          }
        }
      }
    }
  }

  &__aside-total {
    margin-bottom: 40px;

    p {
      margin-bottom: 10px;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      color: #000;
    }

    .cart-content__total {
      margin-top: 7px;
      padding-top: 12px;
      border-top: 1px solid #000;
      font-weight: 700;
      font-size: 18px;
      line-height: 21px;
      color: #000;
    }
  }

  &__aside-info {
    margin-bottom: 60px;

    h2 {
      margin-bottom: 47px;
      color: var(--basic-green);
      font-weight: 700;
      font-size: 18px;
      line-height: 1;
      letter-spacing: 0.045em;
      text-transform: uppercase;

      @media (max-width: 480px) {
        font-size: 15px;
      }

      & + h4 {
        text-transform: none;
      }
    }

    h4 {
      margin-top: 15px;
      margin-bottom: 5px;
      font-weight: 700;
      font-size: 12px;
      line-height: 1;
      letter-spacing: -0.022em;
      color: #000;
      text-transform: uppercase;
    }

    p {
      margin-bottom: 5px;
      font-weight: 400;
      font-size: 12px;
      line-height: 1;
      letter-spacing: -0.022em;
      color: #000;

      span {
        position: relative;

        &::after {
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 1px;
          content: "";
          background-color: #000;
        }
      }
    }

    a {
      position: relative;
      font-weight: 700;
      color: var(--basic-green);
      transition: 0.3s ease-out;

      &:not([href^="tel:"]) {
        &::after {
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 1px;
          content: "";
          background-color: var(--basic-green);
          transition: 0.3s ease-out;
        }
      }

      &:hover {
        color: var(--basic-green);
        opacity: 0.7;
        text-decoration: none;

        &::after {
          background-color: rgba(#5dbd00, 0.7);
        }
      }
    }
  }

  &__warning {
    margin: 0 -23px 12px -23px;
    padding: 13px 19px;
    background-color: var(--neon);
    font-weight: 700;
    font-size: 18px;
    line-height: 1;
    color: #000;
    letter-spacing: -0.01em;
    text-align: center;

    @include media-breakpoint-down(md) {
      padding: 15px 28px;
      font-size: 16px;
    }

    @media (max-width: 375px) {
      padding: 13px 15px;
      font-size: 14px;
    }
  }
}
