.product-tabs {
  border: 1px solid #d0d0d0;

  &__content {
    display: none;

    &.show {
      display: block;
    }
  }

  &__nav {
    display: flex;
    background-color: #e0e0e0;
  }

  &__link {
    padding: 9px 22px;
    font-weight: 700;
    font-size: 13px;
    line-height: 1;
    color: #000;
    background-color: #e0e0e0;
    transition: 0.3s ease-out;
    border-bottom: 1px solid #d0d0d0;
    letter-spacing: 0.01em;

    @include media-breakpoint-down(md) {
      padding: 9px 11px;
      font-size: 11px;
    }

    @media (max-width: 375px) {
      font-size: 10px;
    }

    &:hover {
      text-decoration: none;
      color: rgba(#000, 0.7);
    }

    &:not(:last-of-type) {
      border-right: 1px solid #d0d0d0;
    }

    &:last-of-type {
      flex-grow: 1;
    }

    &.active {
      background-color: #fff;
      border-bottom: none;
    }
  }

  &__content {
    padding: 30px 33px 20px;

    @include media-breakpoint-down(md) {
      padding: 30px 10px 20px;
    }

    h3 {
      margin-bottom: 16px;
      font-weight: 800;
      font-size: 20px;
      line-height: 1;
      letter-spacing: 0.01em;
      color: #000;
      text-transform: uppercase;

      @include media-breakpoint-down(md) {
        margin-bottom: 11px;
      }
    }

    p {
      margin-bottom: 0;
      font-weight: 400;
      font-size: 14px;
      line-height: 1;
      color: #000;
    }
  }
}
