.account-tienda {
  font-family: Mulish, sans-serif;
  .search {
    height: 49px;
    background-color: #efefef;
    padding: 16px 20px;
    font-size: 18px;
    font-weight: 600;
    line-height: 18px;
    letter-spacing: -0.022em;
    border: none;
    margin-left: 8px;
    @include media-breakpoint-down(md) {
      margin-left: 0;
    }
  }
  .search-button {
    right: 18px;
    bottom: 11px;
    border: none;
    background-color: transparent;

    &:hover,
    &:focus {
      background-color: transparent;
    }
  }
  .account-tienda__search {
    background-color: #efefef;
  }
  .account__user-img {
    border-radius: 50%;
    width: 36px;
    height: 36px;
    left: -13px;
    top: 50%;
    transform: translateY(-50%);
  }
  .account__user-link {
    padding-left: 15px;
  }
  .account__user-link span {
    padding-left: 36px;
  }
  @include media-breakpoint-down(sm) {
    .box2 {
      order: 0;
    }
    .box1 {
      order: 1;
    }
    .box3 {
      order: 2;
    }
  }
}
