.form-subscribe {
  width: 100%;
  //max-width: 343px;
  background-color: var(--neon);


  &__title {
    padding: 15px 10px;
    background-color: var(--basic-gray);
    display: flex;
    justify-content: center;

    @include media-breakpoint-down(md) {
      margin-bottom: 12px;
      font-size: 18px;
      line-height: 27px;
    }

    h3 {
      margin-left: 8px;
      margin-bottom: 0;
      font-weight: 800;
      font-size: 16px;
      line-height: 1;
      color: #fff;
    }
  }

  &__content {
    padding: 20px 13px;
    background-color: var(--neon);

    p {
      margin-bottom: 35px;
      font-weight: 400;
      font-size: 13px;
      line-height: 1;
      color: #000;
    }
  }

  .input {
    margin-bottom: 30px;
    width: 100%;

    label {
      top: 15px;
      left: 0;

      font-weight: 400;
      font-size: 13px;
      line-height: 0;
      color: #000;
      transition: 0.3s ease-out;
    }

    input {
      padding: 22px 0 3px;
      width: 100%;
      background-color: transparent;
      border: none;
      border-bottom: 2px solid #000;
      font-size: 15px;
      line-height: 1;

      &:focus {
        outline: none;
        // & + label {
        //   opacity: 0;
        //   visibility: hidden;
        // }
      }
    }
  }

  .btn + p {
    margin-top: 37px;
    font-size: 9px;
    margin-bottom: 0;
    width: 100%;
    align-self: flex-start;

    span {
      display: block;
    }
  }
}
