.product-table {

  h2 {
    margin-bottom: 20px;
    font-weight: 800;
    font-size: 24px;
    line-height: 1;
    color: #000;
    letter-spacing: 0.01em;

    @include media-breakpoint-down(md) {
      margin-bottom: 11px;
      font-size: 14px;
    }
  }

  table {
    width: 100%;
    margin-bottom: 50px;

    @include media-breakpoint-down(md) {
      margin-bottom: 30px;
    }

    &:nth-of-type(1) {
      th {
        &:nth-of-type(1) {
          width: 18.3%;
        }

        &:nth-of-type(2) {
          width: 24.6%;
        }

        &:nth-of-type(3) {
          //width: 15.1%;
          display: flex;
          justify-content: center;
          align-items: center;

          @include media-breakpoint-down(md) {
            width: 15.1%;
            display: table-cell;
          }

          p {
            margin-left: 3px;

            @include media-breakpoint-down(md) {
              margin-left: 0;
              margin-top: 3px;
            }
          }
        }

        &:nth-of-type(4) {
          width: 12.4%;
        }

        &:nth-of-type(5) {
          width: 17.8%;
        }

        &:nth-of-type(6) {
          width: 11.7%;
        }
      }
    }

    &:nth-of-type(2) {
      th {
        &:nth-of-type(1) {
          width: 18.3%;
        }

        &:nth-of-type(2) {
          width: 29.9%;
        }

        &:nth-of-type(3) {
          width: 16.9%;
        }

        &:nth-of-type(4) {
          width: 19%;
        }

        &:nth-of-type(5) {
          width: 16.5%;
        }
      }
    }
  }

  th, td {
    border: 1px solid #e8e9eb;
  }

  th {
    height: 87px;
    box-sizing: border-box;
    background-color: #d3d9d2;
    font-weight: 800;
    font-size: 18px;
    line-height: 21px;
    color: #000;
    text-align: center;
    vertical-align: middle;
    letter-spacing: -0.005em;
    text-transform: uppercase;

    @include media-breakpoint-down(md) {
      font-size: 12px;
      line-height: 12px;
      letter-spacing: -0.045em;
    }

    @media (max-width: 375px) {
      font-size: 7px;
      letter-spacing: -0.065em;
    }

    img {
      @include media-breakpoint-down(md) {
        transform: scale(0.4);
      }
    }
  }

  tr {
    &:nth-of-type(even) {
      background-color: #ebf1da;
    }
  }

  td {
    height: 31px;
    box-sizing: border-box;
    font-weight: 600;
    font-size: 17px;
    line-height: 20px;
    color: #000;
    text-align: center;
    vertical-align: middle;

    @include media-breakpoint-down(md) {
      font-size: 10px;
      line-height: 12px;
    }

    @media (max-width: 375px) {
      font-size: 8px;
    }
  }
}
