.page-content {
  padding: 28px 0 110px;

  @include media-breakpoint-down(md) {
    padding: 28px 0;
  }

  h1 {
    margin-top: 37px;
    margin-bottom: 25px;
    font-size: 36px;
    line-height: 42px;
    font-weight: 700;
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    color: #000;
  }

  h2 {
    margin-bottom: 20px;
    font-size: 36px;
    line-height: 42px;
    font-weight: 700;
    font-family: "Roboto", sans-serif;
    color: #000;
  }

  p {
    margin-bottom: 15px;
    font-size: 14px;
    line-height: 16px;
    font-weight: 400;
    font-family: "Roboto", sans-serif;
    color: #000;

    span {
      display: block;
      font-family: "Roboto", sans-serif;
    }
  }

  ul {

    li {
      margin-bottom: 20px;
      font-family: "Roboto", sans-serif;
    }

    h3 {
      position: relative;
      margin-bottom: 20px;
      font-size: 24px;
      line-height: 28px;
      font-weight: 700;
      font-family: "Roboto", sans-serif;
      color: #000;
    }

    ul {
      list-style-type: disc;
      margin-left: 20px;
      margin-bottom: 20px;

      li {
        margin-bottom: 0;
        font-family: "Roboto", sans-serif;
      }

      ul {
        p {
          margin-bottom: 0;
          display: inline-block;
          font-family: "Roboto", sans-serif;

          span {
            display: inline;
            font-weight: 700;
            font-family: "Roboto", sans-serif;
          }
        }
      }
    }

    a {
      text-decoration: underline;
      transition: 0.3s ease-out;
      color: var(--basic-green);
      font-family: "Roboto", sans-serif;

      &:hover {
        color: var(--basic-green);
        opacity: 0.7;
      }
    }
  }

  ol {
    list-style: auto;
    counter-reset: li;

    li {
      list-style-type: none;
      margin-bottom: 20px;

      h3 {
        position: relative;
        padding-left: 35px;
        margin-bottom: 20px;
        font-size: 24px;
        line-height: 28px;
        font-weight: 700;
        font-family: "Roboto", sans-serif;
        color: #000;

        &::before {
          position: absolute;
          top: 0;
          left: 0;

          font-size: 24px;
          line-height: 28px;
          font-weight: 700;

          width: 28px;
          height: 28px;
          display: flex;
          justify-content: center;
          align-items: center;
          color: #000;
          content: counter(li) ".";
          counter-increment: li;
          font-family: "Roboto", sans-serif;
        }
      }
    }

    ol {
      counter-reset: li2;

      li {
        list-style-type: none;

        h4 {
          position: relative;
          padding-left: 35px;
          margin-bottom: 20px;
          font-size: 18px;
          line-height: 21px;
          font-weight: 700;
          font-family: "Roboto", sans-serif;
          color: #000;

          &::before {
            position: absolute;
            top: 0;
            left: 0;

            width: 28px;
            height: 21px;
            counter-increment: li2;
            content: counter(li) "." counter(li2);
          }
        }
      }
    }
  }

  &__wrapper {
    margin: 0 auto;
    max-width: 824px;
  }

  &__table {
    margin-bottom: 20px;

    th, td {
      padding: 10px 16px;
      border: 1px solid #e8e9eb;
      color: #676f7c;
      font-size: 14px;
      line-height: 16px;

      @include media-breakpoint-down(md) {
        padding: 10px 6px;
      }
    }

    th {
      font-weight: 400;
      text-transform: uppercase;
    }

    &--small {
      max-width: 725px;

      th {
        background-color: #e4e4e4;

        &:nth-of-type(1) {
          width: 15.9%;
        }

        &:nth-of-type(2) {
          width: 32.5%;
        }

        &:nth-of-type(3) {
          width: 51.6%;
        }
      }

      th, td {
        font-size: 16px;
        line-height: 20px;

        @include media-breakpoint-down(md) {
          font-size: 14px;
          line-height: 16px;
        }
      }
    }
  }
}
