.card-mini {
  border-color: #fff;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-width: 224px;

  @include media-breakpoint-down(sm) {
    min-width: auto;
  }

  &:hover {
    text-decoration: none;

    h3 {
      color: var(--basic-green);
    }

    p {
      color: var(--basic-green);
    }
  }

  &__badge {
    top: 0;
    right: 0;
    border: 23px solid transparent;
    border-top: 23px solid #eb5757;
    border-right: 23px solid #eb5757;
    text-align: center;

    span {
      position: absolute;
      top: -18px;
      left: -8px;
      font-weight: 900;
      font-size: 10px;
      line-height: 1;
      letter-spacing: 0.01em;
      color: #fff;
      transform: rotate(45deg);
    }
  }

  &__info {
    padding: 34px 10px 23px;
    background-color: #fff;
    flex-grow: 1;
    border-top: 2px solid #565657;

    h3 {
      margin-bottom: 11px;
      font-weight: 800;
      font-size: 18px;
      line-height: 1;
      color: #000;
      transition: 0.3s ease-out;
    }

    p {
      font-weight: 400;
      font-size: 14px;
      line-height: 1;
      color: #000;
      transition: 0.3s ease-out;
    }

    img {
      margin-bottom: 22px;
      width: 100%;

      @include media-breakpoint-down(md) {
        margin-bottom: 11px;
      }
    }
  }

  &__price {
    margin-top: auto;
    padding: 8px;
    background-color: var(--basic-green);
    display: flex;
    justify-content: center;

    span {
      font-weight: 800;
      font-size: 18px;
      line-height: 1;

      color: #fff;
    }
  }
}
