.stepper {
  position: relative;

  display: flex;
  flex-direction: column;
  padding: 0 3px;

  &:hover,
  &:focus,
  &--active {
    opacity: 1;
  }

  span {
    margin-bottom: 9px;
    font-weight: 700;
    font-size: 15px;
    line-height: 22px;
    letter-spacing: 0.02em;
    color: #000;
    text-transform: uppercase;
  }

  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type="number"] {
    -moz-appearance: textfield;
  }

  input[type="number"]:hover,
  input[type="number"]:focus {
    -moz-appearance: number-input;
  }
}

.stepper__form {
  padding: 10px 18px;
  display: flex;
  align-items: center;
  height: 100%;
  max-width: 104px;

  border: 1px solid #cfcece;
}

.stepper input {
  margin: 0 8px;
  width: 30px;
  text-align: center;

  border: none;
}

.stepper__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  line-height: 22px;
  height: 100%;
  padding: 0;
  color: #666;

  border: 0;
  background-color: transparent;
  outline: none;
  cursor: pointer;

  &:hover,
  &:focus {
    color: $black;
    outline: none;
  }
