.btn {
  border-radius: 0;

  transition: 0.3s ease-out;

  &:focus {
    outline: none;
  }

  span {
    &.hide {
      display: none;
    }
  }
}

.btn-light {
  color: var(--basic-green);
  background-color: white;
  text-transform: uppercase;
  font-size: 18px;
  font-style: normal;
  font-weight: 800;
  line-height: 22px;
  letter-spacing: 0.001em;
  border: 1px solid var(--basic-green);
  padding: 13px 25px 12px 21px;
  transition: 0.3s ease-out;
  &:hover {
    border: 1px solid white;
    color: white;
    background-color: var(--basic-green);
  }
}

.btn-black {
  font-weight: 800;
  font-size: 18px;
  line-height: 1;
  letter-spacing: 0.001em;
  background-color: #000;
  color: #fff;
  text-transform: uppercase;
  width: 100%;
  //max-width: 344px;
  padding: 13px;
  border: 1px solid #000;

  &:hover {
    color: #000;
    background-color: white;
  }

  &--text-color {
    color: var(--basic-green);
    max-width: 170px;

    &:hover {
      color: #000;
      background-color: #fff;
    }
  }
}
