.input {
  margin-bottom: 30px;
  width: calc((100% - 15px) / 2);

  @include media-breakpoint-down(md) {
    width: 100%;
  }

  label {
    top: 0;
    left: 0;

    font-size: 13px;
    line-height: 1;
  }

  input {
    padding: 22px 0 3px;
    width: 100%;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid var(--basic-gray);
    font-size: 15px;
    line-height: 1;
    color: var(--basic-gray);
    transition: 0.3s ease-out;

    &:focus {
      outline: none;
      border-bottom: 1px solid var(--basic-green);
    }

    &[type=checkbox] {
      position: absolute;
      bottom: 0;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      border: 0;
      clip: rect(0 0 0 0);
      text-align: center;

      & + label {
        position: relative !important;
        top: auto;
        left: auto;

        padding-left: 25px;

        font-weight: 400;
        font-size: 13px;
        line-height: 1;
        color: var(--basic-gray);
        transition: 0.3s ease-out;
        cursor: pointer;

        a {
          position: relative;
          color: var(--basic-gray);
          text-decoration: none;
          transition: 0.3s ease-out;

          &::after {
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 1px;
            background-color: var(--basic-gray);
            content: "";
            transition: 0.3s ease-out;
          }

          &:hover {
            color: var(--basic-green);
            text-decoration: none;

            &::after {
              background-color: var(--basic-green);
            }
          }
        }

        &::after {
          position: absolute;
          width: 15px;
          height: 15px;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          content: " ";
          background-color: tarnsparent;
          display: flex;
          border: 1px solid var(--basic-gray);
          border-radius: 3px;

          @include media-breakpoint-down(sm) {
            top: 0;
            transform: translateY(0);
          }
        }
      }

      &:checked {
        & + label {
          &:after {
            content: "â";
            color: var(--basic-green);
            font-size: 13px;
            line-height: 1;
            text-align: center;
          }
        }
      }
    }
  }

  &--checkbox {
    margin-top: 10px;
    margin-bottom: 20px;
    width: 100%;

    .input__required {
      display: none;
    }
  }

  &--single {
    width: 100%;

    input {
      width: calc((100% - 15px) / 2);
    }

    @include media-breakpoint-down(md) {

      input {
        width: 100%;
      }
    }
  }

  &--border {
    padding-top: 30px;

    input {
      padding: 20px 5px 5px;
      font-size: 18px;
      border: 1px solid #e7e7e7;
      color: #000;

      & + label {
        font-size: 18px;
        line-height: 1;
        letter-spacing: -0.022em;
        color: #000;

        @media (min-width: 992px) and (max-width: 1200px) {
          font-size: 15px;
        }
      }

      &:focus {
        border-color: var(--basic-green);
      }
    }

    p {
      margin-top: 7px;
      margin-bottom: 0;
      font-weight: 400;
      font-size: 12px;
      line-height: 1;
      color: #000;
      letter-spacing: -0.022em;
    }
  }

  &--radio {
    margin-bottom: 0;
    width: 100%;

    input[type=radio] {
      position: absolute;
      bottom: 0;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      border: 0;
      clip: rect(0 0 0 0);

      & + label {
        position: relative !important;
        top: auto;
        left: auto;
        padding: 23px 20px 23px 56px;
        display: flex;
        align-items: center;
        width: 100%;

        font-weight: 400;
        font-size: 18px;
        line-height: 1;
        color: #000;
        transition: 0.3s ease-out;

        &::after {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 20px;
          content: " ";
          background-color: #fff;
          display: flex;
          border: 1px solid var(--basic-green);
          text-align: center;
          justify-content: center;
          width: 26px;
          height: 26px;
          border-radius: 50%;
          cursor: pointer;

          @media (max-width: 480px) {
            left: 10px;
            top: 13px;
            transform: none;
            width: 20px;
            height: 20px;
          }
        }

        @media (max-width: 480px) {
          padding: 15px 10px 15px 36px;
          font-size: 13px;
        }
      }

      &:checked {
        & + label {
          background-color: rgba(#5dbd00, 0.2);

          &::before {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 26px;
            z-index: 1;
            content: "";
            width: 14px;
            height: 14px;
            background-color: var(--basic-green);
            border-radius: 50%;

            @media (max-width: 480px) {
              left: 15px;
              top: 18px;
              transform: none;
              width: 10px;
              height: 10px;
            }
          }
        }
      }
    }

    .input__price {
      margin-left: auto;
      font-weight: 700;
      font-size: 18px;
      line-height: 1;
      color: #000;
      letter-spacing: -0.022em;
      white-space: nowrap;

      @media (max-width: 480px) {
        font-size: 13px;
      }
    }

    .input__description {
      display: inline-block;
      margin-left: 10px;
      font-weight: 400;
      font-size: 13px;
      line-height: 1;
      color: var(--basic-gray);
      letter-spacing: -0.022em;

      @media (max-width: 480px) {
        font-size: 11px;
      }
    }
  }
}
