.slider-nav {
  z-index: 2;
  display: flex;
  width: 100%;

  &__btn--prev {
    left: 0;
  }


  &__btn--next {
    right: 0;
    svg {
      transform: rotate(180deg);
    }
  }

  &__btn {
    z-index: 0;
    color: $black;
    padding: 0;
    background-color: rgba(#000, 0.6);

    width: 40px;
    height: 40px;
    outline: none;

    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
      background-color: rgba(#000, 0.4);
    }

    &:focus {
      outline: none;
      background-color: rgba(#000, 0.4);
    }

    @include media-breakpoint-down(md) {
      width: 24px;
      height: 24px;
    }

    &::before {
      display: none;
    }

    svg {
      @include media-breakpoint-down(md) {
        width: 10px;
        height: 19px;
      }
    }
  }
}
