.breadcrumbs {
  &__link {
    margin-bottom: 5px;
    position: relative;
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: -0.022em;
    text-transform: uppercase;
    color: var(--basic-gray);
    transition: 0.3s ease-out;

    &.active {
      font-weight: 800;
      color: var(--basic-green);
      pointer-events: none;
    }

    &:not(:last-of-type) {
      margin-right: 20px;

      &:after {
        position: absolute;
        content: "/";
        right: -13px;
      }
    }

    &:hover {
      text-decoration: none;
      color: var(--basic-gray);
      opacity: 0.7;
    }
  }
}
