.rounded-1px {
  border-radius: 1px;
}
.rounded-2px {
  border-radius: 2px;
}
.rounded-6px {
  border-radius: 6px;
}
.tt-n {
  text-transform: none;
}
.cursor-pointer {
  cursor: pointer;
}
.absolute-fill {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.object-fit-img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
