.index {
  a {
    text-decoration: none;
    &:hover {
      text-decoration: none;
    }
  }
}
.menu-button {
  height: 100px;
  width: 100px;
  display: none;
  border: none;
  color: white;
  font-size: 12px;
  font-weight: 800;
  outline: none;
  &:focus {
    outline: none;
  }
  @include media-breakpoint-down(md) {
    display: block;
  }
}
.aside-menu {
  position: fixed;
  z-index: 3;
  top: 215px;
  height: 215px;
  right: 0;
  padding-top: 30px;
  padding-bottom: 33px;
  background: url("images/aside-bg.png") right center no-repeat;
  display: none;
  @include media-breakpoint-down(md) {
    display: block;
    opacity: 0;
    pointer-events: none;
    background-color: white;
    background-image: none;
    padding-left: 30px;
    border-bottom-left-radius: 30px;
    border-top-left-radius: 30px;
    top: 100px;
    transition: all 0.3s;
  }
  &.visible {
    opacity: 1;
    pointer-events: inherit;
  }
  ul {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: end;
    gap: 17px;
  }
  li {
    margin-left: auto;
  }
  a {
    color: rgba(255, 255, 255, 0.7);
    opacity: 0.6;
    font-size: 12px;
    font-weight: 600;
    line-height: 12px;
    letter-spacing: -0.022em;
    padding-right: 67px;
    transition: all 0.3s;
    @include media-breakpoint-down(md) {
      color: var(--basic-gray);
      opacity: 1;
      font-size: 16px;
    }
    &:after {
      position: absolute;
      content: url("images/aside-dot.svg");
      right: 5px;
      @include media-breakpoint-down(md) {
        content: url("images/aside-dot-mob.svg");
      }
    }
  }
  a:hover {
    color: var(--neon);
    font-size: 16px;
    font-weight: 800;
    opacity: 1;
    @include media-breakpoint-down(md) {
      font-weight: 600;
      color: var(--basic-green);
    }
    &:after {
      content: url("images/aside-dot-active.svg");
      @include media-breakpoint-down(md) {
        content: url("images/aside-dot-active-mob.svg");
      }
    }
  }
}
.hero {
  height: 664px;
  background: url("images/index/hero-bg.png") center center no-repeat;
  background-size: cover;
  @include media-breakpoint-down(md) {
    height: 364px;
  }
}
.hero__page-link {
  display: block;
  text-align: center;
  line-height: 100px;
  width: 100px;
  height: 100px;
  color: white;
  font-size: 12px;
  font-weight: 800;
  &:hover {
    color: white;
  }
}
.hero__language {
  padding-right: 16px;
  display: flex;
  height: 100px;
  background-color: var(--basic-green);
  @include media-breakpoint-down(md) {
    display: none;
  }
}
.hero__language-link {
  margin: 35px 10px 0;
}
.hero__logo {
  position: absolute;
  top: 50%;
  left: 75%;
  transform: translate(-50%, -50%);
  @include media-breakpoint-down(md) {
    left: 50%;
  }
}
.account {
  height: 49px;
  @include media-breakpoint-down(md) {
    height: auto;
  }
  [class*="col-"] {
    margin-left: 45px;
    @include media-breakpoint-down(lg) {
      margin-left: 0;
    }
  }
  [class*="col-"] ~ [class*="col-"] {
    border-left: 2px solid #598034;
    @include media-breakpoint-down(md) {
      border: none;
    }
  }
}
.account__link, .account__link:hover {
  color: white;
}
.account__link {
  display: block;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: -0.022em;
  text-align: center;
  svg {
    margin-right: 12px;
    margin-top: -4px;
  }
  @include media-breakpoint-down(md) {
    height: 49px;
    line-height: 49px;
  }
}
.account__link-colour {
  @include media-breakpoint-down(md) {
    background-color: var(--basic-green);
  }
}
.us__wrapper {
  h2 {
    line-height: 48px;
    margin-bottom: 19px;
  }
}
.us__info {
  max-width: 366px;
  line-height: 22px;
  letter-spacing: 0.3px;
}
.anchor-light {
  color: rgba(255, 255, 255, 0.6);
}
.anchor-dark {
  color: rgba(87, 87, 86, 0.6);
}
.anchor-text {
  font-size: 14px;
  font-weight: 900;
  line-height: 14px;
  letter-spacing: 0.001em;
  position: relative;
  margin-bottom: 44px;
  text-transform: uppercase;
  &:after {
    content: "";
    position: absolute;
    left: 0;
    width: 55px;
    height: 3px;
    bottom: -10px;
  }
}
.white-underline {
  &:after {
    background-color: white;
  }
}
.green-underline {
  &:after {
    background-color: var(--basic-green);
  }
}
.products {
  position: absolute;
  left: 100px;
  top: 0;
  z-index: 2;
  max-width: 453px;
  background-color: white;
  color: var(--basic-gray);
  padding: 125px 0 0;
  @include media-breakpoint-down(lg) {
    max-width: 350px;
  }
  @include media-breakpoint-down(md) {
    padding: 50px 0 0;
    position: inherit;
    max-width: inherit;
  }
  &__wrapper {
    padding: 0 54px 0 64px;
    @include media-breakpoint-down(lg) {
      padding: 0 15px;
    }
  }
  h1 {
    line-height: 58px;
    span {
      font-weight: 900;
      color: var(--basic-green);
      line-height: 48px;
      text-transform: uppercase;
    }
  }
  &__info {
    line-height: 22px;
    letter-spacing: 0.3px;
    padding-top: 22px;
    padding-bottom: 54px;
  }
  &__list-info {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    margin-bottom: 19px;
    max-width: 200px;
  }
  &__list-title {
    color: var(--basic-black);
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 19px;
    span {
      font-weight: 900;
      font-size: 36px;
      line-height: 24px;
      letter-spacing: 0.1px;
      padding-right: 8px;
    }
  }
  &__link {
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: var(--basic-green);
    &:hover {
      color: var(--basic-green);
    }
  }
  .products__list {
    @include media-breakpoint-down(md) {
      max-width: 400px;
    }
  }
  li {
    margin-bottom: 68.5px;
    background-repeat: no-repeat;
    background-position: center center;
    @include media-breakpoint-down(lg) {
      margin-bottom: 93px;
    }
    @include media-breakpoint-down(md) {
      margin-bottom: 50px;
    }
  }
  li:nth-of-type(1) {
    background-image: url("images/index/prod-1.png");
  }
  li:nth-of-type(2) {
    background-image: url("images/index/prod-2.png");
  }
  li:nth-of-type(3) {
    background-image: url("images/index/prod-3.png");
  }
}
.products, .account {
  input {
    background-color: #efefef;
    padding: 16px 27px;
    font-size: 18px;
    font-weight: 600;
    line-height: 18px;
    letter-spacing: -0.022em;
    border: none;
    &::-ms-clear {
      display: none;
      width: 0;
      height: 0;
    }
    &::-webkit-search-decoration,
    &::-webkit-search-cancel-button,
    &::-webkit-search-results-button,
    &::-webkit-search-results-decoration { display: none; }
  }
  .search-button {
    right: 25px;
    bottom: 11px;
    border: none;
    background-color: transparent;
  }
}
.commitment {
  background: url("images/index/commitment-bg.png") center center no-repeat;
  background-size: cover;
  h2 {
    font-weight: 800;
  }
}
.commitment-text {
  line-height: 22px;
  letter-spacing: 0.003em;
  max-width: 688px;
  margin-top: 20px;
}
.services {
  padding: 115px 0 110px;
  overflow: hidden;
  h2 {
    margin-bottom: 72px;
    @include media-breakpoint-down(md) {
      margin-bottom: 44px;
    }
  }
}
.services__card {
  display: grid;
  grid-template-columns: minmax(0, 165px) 1fr minmax(0, 165px);
  row-gap: 49px;
  @include media-breakpoint-up(xl) {
    margin: auto;
  }
  @include media-breakpoint-down(sm) {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  @media (max-width: 375px) {
    grid-template-columns: minmax(0, 165px) minmax(0, 165px);
  }
}
.services__card-big {
  @include media-breakpoint-down(sm) {
    display: none;
  }
}
.services__card-small {
  @include media-breakpoint-up(md) {
    display: none;
  }
}
.services__card-wrapper {
  &:after {
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    background-color: var(--basic-gray-light);
    width: 100%;
    @include media-breakpoint-up(xl) {
      content: none;
    }
    @include media-breakpoint-down(sm) {
      content: none;
    }
  }
  display: flex;
  @include media-breakpoint-down(sm) {
    flex-direction: column;
    height: fit-content;
    margin-bottom: 20px;
  }
  h3 {
    line-height: 36px;
    color: var(--basic-gray);
    padding-left: 20px;
    padding-right: 39px;
    margin: auto;
    letter-spacing: -0.022em;
    font-weight: 500;
    @include media-breakpoint-down(sm) {
      padding: 7px;
      margin: 0;
      word-wrap: break-word;
    }
    @media (max-width: 375px) {
      font-size: 25px;
      line-height: 26px;
    }
  }
  &:nth-of-type(odd) {
    grid-column-start: 2;
    grid-column-end: -1;
    position: relative;
    @include media-breakpoint-down(lg) {
      grid-column-start: 1;
      grid-column-end: -1;
    }
    @include media-breakpoint-down(sm) {
      grid-column-start: 1;
      grid-column-end: -2;
    }
    &:after {
      right: 1px;
      transform: translateX(100%);
    }
    p {
      @include media-breakpoint-up(md) {
        padding-right: 50px;
      }
    }
  }
  &:nth-of-type(even) {
    grid-column-start: 1;
    grid-column-end: -2;
    position: relative;
    @include media-breakpoint-down(lg) {
      grid-column-start: 1;
      grid-column-end: -1;
    }
    @include media-breakpoint-down(sm) {
      grid-column-start: 2;
      grid-column-end: -1;
    }
    &:after {
      left: 1px;
      transform: translateX(-100%);
    }
    p {
      @include media-breakpoint-up(md) {
        text-align: right;
        padding-left: 50px;
      }
    }
    h3 {
      padding-left: 27px;
      padding-right: 19px;
      @include media-breakpoint-up(md) {
        text-align: right;
      }
      @include media-breakpoint-down(sm) {
        padding: 7px;
      }
    }
  }
  .bg-green {
    padding: 13px;
    height: 100%;
    display: flex;
    svg {
      margin: auto;
      @include media-breakpoint-down(sm) {
        width: 116px;
        height: 116px;
      }
      @media (max-width: 345px) {
        width: auto;
        height: auto;
      }
    }
  }
  p {
    font-family: Inter, sans-serif;
    font-size: 13px;
    font-weight: 300;
    line-height: 13px;
    letter-spacing: -0.022em;
    color: #000;
    @include media-breakpoint-down(sm) {
      letter-spacing: -0.03em;
      padding: 7px;
      margin: 0;
    }
  }
}
.advantages {
  overflow: hidden;
  .slick-list {
    overflow: inherit;
  }
}
.advantages__wrapper {
  margin-top: 57px;
}
.advantages__slide {
  padding: 0 5px;
  position: relative;
  @include media-breakpoint-down(sm) {
    display: flex;
    gap: 22px;
    margin-bottom: 45px;
    padding: 0 15px;
  }
  &:nth-of-type(even) {
    flex-direction: row-reverse;
  }
  img {
    width: 100%;
    @include media-breakpoint-down(sm) {
      object-fit: cover;
      width: 130px;
    }
  }
  &-text {
    background: rgba(255, 255, 255, 0.7);
    position: absolute;
    min-height: 208px;
    bottom: 0;
    left: 16px;
    right: 16px;
    padding: 12px 10px 12px 11px;
    overflow: hidden;
    color: #000;
    @include media-breakpoint-down(sm) {
      position: inherit;
      height: inherit;
      left: inherit;
      right: inherit;
      padding: 0;
      background-color: var(--basic-green);
      color: white;
    }
  }
  &-info {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -0.3px;
  }
  &:nth-of-type(even) {
    text-align: right;
  }
  &-title {
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: -0.2px;
    margin-bottom: 12px;
    @include media-breakpoint-down(sm) {
      font-size: 18px;
    }
  }
}
.advantages, .commitment, .us {
  padding: 100px 0;
}
.services, .advantages, .commitment, .us {
  @include media-breakpoint-down(md) {
    padding: 50px 0;
  }
}
.index .slick-arrow:before {
  display: none;
}
.index .slick-arrow {
  width: 40px;
  height: 40px;
  img {
    width: 100%;
  }
}
.index .slick-prev {
  z-index: 2;
  left: 15px;
}
.index .slick-next {
  right: 35px;
}
.hero__menu {
  display: grid;
  grid-template-columns: 100px 1fr auto;
  position: fixed;
  z-index: 1;
  width: 100%;
  @include media-breakpoint-down(md) {
    display: flex;
    justify-content: space-between;
    position: absolute;
  }
}
.hero__menu-block {
  height: 100px;
  display: flex;
  align-items: center;
  background-color: var(--basic-green);
  @include media-breakpoint-down(md) {
    display: none;
  }
}
.hero__menu-list {
  display: flex;
  margin-left: auto;
  a {
    color: white;
    font-size: 14px;
    font-weight: 600;
    line-height: 12px;
    letter-spacing: -0.022em;
    margin-right: 16px;
    transition: all 0.3s;
    position: relative;
    &:hover {
      color: var(--basic-green);
    }
    &:before {
      content: attr(data-name);
      display: block;
      position: absolute;
      top: 3px;
      left: -2px;
      color: var(--basic-green);
      opacity: 0;
      transition: all 0.3s;
      pointer-events: none;
    }
    &:hover:before {
      font-size: 15px;
      font-weight: 700;
      opacity: 1;
      color: var(--neon);
    }
  }
}
