.distributor {
  padding: 22px 60px 37px;
  @include media-breakpoint-down(md) {
    padding: 22px 0 37px;
  }
}
.distributor__materials {
  margin-top: 28px;
  display: grid;
  grid-template-columns: 1fr minmax(200px, 262px);
  gap: 39px;
  @include media-breakpoint-down(md) {
    grid-template-columns: 1fr;
  }
}
.distributor__materials-area {
  .show-more {
    width: max-content;
    font-size: 18px;
    font-style: normal;
    font-weight: 800;
    line-height: 18px;
    text-decoration: underline;
    color: var(--basic-green);
    @include media-breakpoint-down(sm) {
      font-size: 14px;
    }
  }
}
.breadcrumb {
  margin-bottom: 5px;
}
.crumb {
  position: relative;
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: -0.022em;
  text-transform: uppercase;
  @include media-breakpoint-down(md) {
    font-size: 14px;
  }
  @include media-breakpoint-down(sm) {
    font-size: 12px;
  }
  &.active {
    font-weight: 800;
    color: var(--basic-green);
  }
  &:not(:last-of-type) {
    margin-right: 20px;
    &:after {
      position: absolute;
      content: "/";
      right: -13px;
    }
  }
}
.filter-mob:hover {
  text-decoration: none;
  color: var(--basic-green);
}
.distributor__materials-number {
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
  letter-spacing: 0.01em;
  color: #9a9a9a;
  @include media-breakpoint-down(sm) {
    font-size: 10px;
  }
}
.distributor__materials-filters {
  background-color: #f0f8e8;
  padding: 10px;
  margin-bottom: 68px;
  @include media-breakpoint-down(md) {
    display: none;
  }
  &__top {
    border-bottom: 2px solid black;
    margin-bottom: 16px;
  }
  &__title {
    font-size: 18px;
    font-weight: 700;
    line-height: 27px;
    letter-spacing: -0.01em;
  }
  &__reset {
    font-family: Roboto, sans-serif;
    font-size: 13px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: -0.01em;
    text-align: right;
    text-decoration: underline;
    &:hover {
      color: var(--basic-green);
    }
  }
  &__price {
    font-size: 15px;
    font-weight: 700;
  }
  &__categories {
    border-bottom: 1px solid #9a9a9a;
    border-top: 1px solid #9a9a9a;
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
.distributor__materials-area-header {
  display: flex;
  justify-content: space-between;
  @include media-breakpoint-down(md) {
    flex-direction: column;
  }
  .select-css {
    display: block;
    font-family: Nunito, sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 12px;
    padding: 7px 42px 7px 12px;
    box-sizing: border-box;
    margin: 0;
    border: none;
    box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #ececec;
    border-radius: 6px;
    background-image: url("data:image/svg+xml,%3Csvg width='18' height='10' viewBox='0 0 18 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.9106 7.05136C9.66907 7.29282 9.34152 7.42846 9 7.42846C8.65847 7.42846 8.33093 7.29282 8.0894 7.05136L2.18275 1.146C1.93983 0.911381 1.61449 0.78156 1.27678 0.784494C0.939081 0.787429 0.616041 0.922885 0.37724 1.16169C0.138439 1.40049 0.00298333 1.72353 4.87833e-05 2.06123C-0.00288577 2.39893 0.126935 2.72428 0.361551 2.96719L6.2682 8.86869C6.9928 9.59307 7.97543 10 9 10C10.0246 10 11.0072 9.59307 11.7318 8.86869L17.6385 2.96204C17.8731 2.71913 18.0029 2.39378 18 2.05608C17.997 1.71838 17.8616 1.39533 17.6228 1.15653C17.384 0.917732 17.0609 0.782277 16.7232 0.779343C16.3855 0.776408 16.0602 0.906229 15.8173 1.14084L9.9106 7.05136Z' fill='%23666666'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat, repeat;
    background-position: right 0.7em top 50%, 0 0;
    background-size: 18px auto, 100%;
    max-width: 210px;
    @include media-breakpoint-down(md) {
      margin-left: auto;
    }
    @include media-breakpoint-down(sm) {
      margin-top: 16px;
    }
  }
  .select-css::-ms-expand {
    display: none;
  }
}
.distributor_cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;
  margin-top: 34px;
  margin-bottom: 50px;
  @include media-breakpoint-down(lg) {
    grid-template-columns: 1fr 1fr;
  }
  @include media-breakpoint-down(sm) {
    gap: 20px;
    .card-mini__price {
      max-height: 30px;
    }
    .card-mini__price span {
      font-size: 14px;
    }
    .card-mini__info {
      padding: 16px 0;
    }
    .card-mini__info p {
      font-size: 12px;
    }
  }
  @media (max-width: 350px) {
    gap: 5px;
  }
}
.distributor__materials-filters__form {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 5px;
  margin-top: 10px;
  label {
    font-family: Roboto, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: -0.01em;
  }

  input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 50%;
    width: 16px;
    height: 16px;
    border: 1px solid #c4c4c4;
    transition: 0.2s all linear;
    outline: none;
    margin-right: 5px;

    position: relative;
    top: 2px;
  }

  input:checked {
    background-color: #5dbd00;
    border: 3px solid white;
    outline: 1px solid #5dbd00;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    transform: translateZ(0);
  }
}
.distributor__materials-filters__categories-add {
  font-family: Roboto, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: -0.01em;
  text-align: right;
  &:hover {
    color: var(--basic-green);
  }
}
.irs-min, .irs-max {
  display: none !important;
  visibility: hidden !important;
}
.irs {
  margin-bottom: 40px;
}
.irs-from, .irs-to, .irs-single {
  top: 54px !important;
}
.irs--round .irs-from:before, .irs--round .irs-to:before, .irs--round .irs-single:before {
  content: none !important;
}
.irs--round .irs-from, .irs--round .irs-to, .irs--round .irs-single {
  background-color: transparent !important;
  color: #000 !important;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: -0.01em;
}
.irs--round .irs-handle {
  width: 14px !important;
  height: 14px !important;
  top: 30px !important;
  border: 1px solid var(--basic-green) !important;
}
.irs--round .irs-bar {
  background-color: var(--basic-green) !important;
  top: 35px !important;
}
.irs--round .irs-line {
  height: 0.5px !important;
  background-color: #575756bd !important;
  border-radius: 0 !important;
}
