body {
  font-family: Mulish, sans-serif;
}

$font-size: (48px, 48px, 36px, 30px, 22px);


@for $i from 1 through 5 {
  h#{$i} {
    font-size: nth($font-size, $i);
  }
}

h1, h2, h3, h4, h5 {
  font-weight: 700;
}
@include media-breakpoint-down(sm) {
  $font-size: (36px, 34px, 30px, 20px, 18px);


  @for $i from 1 through 5 {
    h#{$i} {
      font-size: nth($font-size, $i);
    }
  }
}
.font-300 {
  font-weight: 300;
}
.font-400 {
  font-weight: normal;
}
.font-500 {
  font-weight: 500;
}
.font-600 {
  font-weight: 600;
}
.font-700 {
  font-weight: bold;
}
.font-900 {
  font-weight: 900;
}


