.footer__up {
  padding-top: 83px;
  @include media-breakpoint-down(sm) {
    padding-top: 50px;
  }
}
.footer__up-form {
  margin-top: 32px;
  margin-bottom: 110px;
  padding-right: 35px;
  @include media-breakpoint-down(sm) {
    margin-top: 0;
    margin-bottom: 50px;
    padding-right: 15px;
  }
}
.footer__form {
  @include media-breakpoint-down(sm) {
    margin-top: 28px;
  }
  label {
    color: white;
    font-size: 13px;
    font-weight: 400;
    line-height: 35px;
    @include media-breakpoint-down(sm) {
      line-height: 17px;
    }
  }
  input {
    background-color: var(--basic-green);
    border: none;
    border-bottom: 1px solid white;
    color: white;
    outline: none;
    @include media-breakpoint-down(sm) {
      margin-bottom: 30px;
    }
  }
  .custom-checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }
  .custom-checkbox + label {
    display: inline-flex;
    align-items: center;
    user-select: none;
    margin-top: 20px;
    line-height: 13px;
  }
  .custom-checkbox + label::before {
    content: "";
    display: inline-block;
    width: 15px;
    height: 15px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid white;
    border-radius: 0.25em;
    margin-right: 0.5em;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    @include media-breakpoint-down(md) {
      margin-top: -8px;
    }
  }
  .custom-checkbox:checked + label::before {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
  }
  button {
    margin-top: 35px;
  }
}
.footer__form-tween {
  gap: 13px;
  display: flex;
  @include media-breakpoint-down(sm) {
    display: inherit;
  }
}
.footer__form-tween-block {
  display: flex;
  flex-direction: column;
  width: 50%;
  @include media-breakpoint-down(sm) {
    width: 100%;
  }
}
.footer__up-social {
  background-color: white;
  padding: 105px 35px 0;
  @include media-breakpoint-down(sm) {
    padding: 50px 0;
  }
}
.footer__social {
  display: flex;
  justify-content: end;
  gap: 28px;
  margin-bottom: 84px;
  @include media-breakpoint-down(sm) {
    justify-content: inherit;
    margin-bottom: 38px;
  }
}
.footer__social-text {
  line-height: 19px;
  text-align: right;
  @include media-breakpoint-down(sm) {
    text-align: left;
  }
}
.footer__social-link {
  font-weight: 700;
  color: var(--basic-gray);
  text-align: right;
  @include media-breakpoint-down(sm) {
    text-align: left;
  }
  &:hover {
    color: var(--basic-gray);
  }
}
.footer__social-link-email {
  margin-top: 40px;
  margin-bottom: 106px;
  @include media-breakpoint-down(sm) {
    margin-top: 30px;
    margin-bottom: 35px;
  }
}
.footer__logo {
  width: fit-content;
  @include media-breakpoint-down(md) {
    width: 100%;
  }
}
.footer__down {
  a {
    display: block;
    color: white;
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: -0.03em;
    padding: 0 7px 0;
    &:hover {
      color: white;
    }
  }
  li ~ li a {
    border-left: 1px solid white;
  }
  ul {
    @include media-breakpoint-down(md) {
      width: 260px;
      flex-wrap: wrap;
      justify-content: center;
      li {
        margin-bottom: 4px;
        margin-top: 4px;
      }
      li:nth-of-type(3) {
        a {
          border-left: none;
        }
      }
    }
  }
}
.footer__down-menu {
  padding: 21px 0 19px;
  @include media-breakpoint-down(sm) {
    padding: 13px 0 11px;
  }
}
.footer__down-copyright {
  @include media-breakpoint-down(sm) {
    background-color: #000;
  }
  p {
    color: white;
    font-size: 12px;
    font-weight: 800;
    line-height: 56px;
  }
}
.footer__down-text-wrapper {
  height: 100%;
  display: grid;
  align-items: center;
  background-color: #000;
}
.footer__contact {
  @include media-breakpoint-down(sm) {
    background-color: white;
  }
}
